.wds-new-feature-status {
	margin-left: 8px;
	margin-top: 6px;
	position: absolute;
	vertical-align: middle;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	right: 10px;

	&,
	&::after,
	&::before {
		background: #1abc9c;
	}

	&::before {
		content: "";
		animation: 1.5s infinite wds-critical-animation;
	}

	&::after {
		content: "";
		animation: 1.5s -.4s infinite wds-critical-animation
	}

	&::after,
	&::before {
		left: 0;
		top: 50%;
		margin-left: -1px;
		margin-top: -5px;
		position: absolute;
		vertical-align: middle;
		width: 10px;
		height: 10px;
		border-radius: 50%
	}

	#wpadminbar & {
		margin-left: 8px;
		margin-top: 9px;
		position: absolute;
		vertical-align: middle;
		width: 8px;
		height: 8px;
		border-radius: 50%;
	}

	.sui-vertical-tab & {
		margin-top: 7px;
	}

	.sui-notice & {
		right: -23px;
		top: -15px;
		margin: 0;
	}
}

@keyframes wds-critical-animation {
	0% {
		transform: scale(1);
		-webkit-transform: scale(1);
		opacity: 1;
	}
	100% {
		transform: scale(2);
		-webkit-transform: scale(2);
		opacity: 0;
	}
}

@-webkit-keyframes wds-critical-animation {
	0% {
		transform: scale(1);
		-webkit-transform: scale(1);
		opacity: 1;
	}
	100% {
		transform: scale(2);
		-webkit-transform: scale(2);
		opacity: 0;
	}
}
